@import url('https://fonts.googleapis.com/css?family=EB+Garamond&display=swap');
@import url('https://fonts.googleapis.com/css?family=PT+Serif&display=swap');

section#post-list {
    width: 100%;
    max-width: 680px;
    margin: 0 auto;
    padding: 0 30px;
}

section#post-list article {
    border: 1px;
    margin-top: 30px;
}

section#post-list article header {
    padding: 20px;
    justify-content: space-between;
}

section#post-list article header .user-info,
.post-title {
    display: flex;
}

section#post-list article header span.title {
    font-size: 40px;
    color: 666;
    margin-top: 3px;
    font-family: 'EB Garamond', serif;
}

section#post-list article header .user-info span.user-img {
    width: 40px;
    height: 40px;
    display: inline-block;
    border-radius: 50%;
    background-image: url(https://media.licdn.com/dms/image/C4D03AQG_eLD5Xr9Yrg/profile-displayphoto-shrink_200_200/0?e=1569456000&v=beta&t=IyWbSGPg8v3dzhKk-No2852hJe_trjZZQdhBpCWuFOU);
    background-size: cover;
 }

 section#post-list article header .user-info div.user-info-details {
    flex-direction: column;
    margin: 8px;
    display: flex;
    text-align: left;
 }

section#post-list article header .user-info div.user-info-details span {
    font-size: 13px;
}

section#post-list article header .user-info div.user-info-details span.postDateTime {
    font-size: 11px;
    color: 666;
    margin-top: 3px;
    color: #a9a9a9;
}

section#post-list article #text {
    padding: 20px;
    text-align: justify;
    text-justify: inter-word;
}

section#post-list article #text p {
    font-family: 'PT Serif', serif;
    font-size: 16px;
    letter-spacing: 0.5px;
    color: #61474b;
    line-height: 1.5em;
    margin-bottom: 1.3em;
    
}

section#post-list article footer {
    padding: 30px;
}

section#post-list article footer p {
    font-size: 13px;
}

section#post-list article footer p span {
    color: #7159c1;
    width: 4em auto;
    height: 2em;
    background: rgba(102, 102, 102, 0.082);
    border: 1px solid #ddd;
    border-radius: 10%;
    text-align: center;
    padding: 3px;
    margin:5px;
}

 